import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Content } from 'ionic-angular';

/**
 * Generated class for the AddSurveyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-survey',
  templateUrl: 'add-survey.html',
})
export class AddSurveyPage {
  @ViewChild(Content) content: Content;
  judulSurvey: any = '';
  type: any = '';
  number = 1;
  surveyList = [{
    number: this.number,
    question: '',
    type: '',
    data: null
  }];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddSurveyPage');
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

  addQuestion() {
    this.number = this.number + 1;
    this.surveyList.push({
      number: this.number,
      question: '',
      type: '',
      data: null
    })

    setTimeout(() => {
      this.content.scrollToBottom(1000);
    });
  }

  delQuestion(index) {
    this.surveyList.splice(index, 1);
  }

  nomorChange(number) {
    //console.log(number);
  }

  onTypeChange(type, index) {
    this.surveyList[index]['data'] = null;

    if (type == 'text') {

    } else if (type == 'multiple' || type == 'checkbox') {
      this.surveyList[index]['data'] = [
        { pilihan: '' }
      ]
    } else if (type == 'linearScale') {
      this.surveyList[index]['data'] = [
        { 
          min: '1',
          max: '10',
          labelMin: '',
          labelMax: ''
        }
      ]
    }
  }

  addMultiple(index) {
    this.surveyList[index]['data'].push({ pilihan: '' });
  }

  delMutiple(indexParent, indexChild) {
    this.surveyList[indexParent]['data'].splice(indexChild, 1);
  }

  addCheckbox(index) {
    this.surveyList[index]['data'].push({ pilihan: '' });
  }

  delCheckbox(indexParent, indexChild) {
    this.surveyList[indexParent]['data'].splice(indexChild, 1);
  }

  convertInt(value) {
    return parseInt(value);
  }

  submit() {
    var surveyData = [];
    surveyData.push({
      survey_title : this.judulSurvey,
      surveyList : this.surveyList
    })
    // console.log(JSON.stringify(surveyData));
  }



}



